<template>
    <views class="home">
        <banner :list="bannerList" :padding="false" :radius="false"></banner>
        <views class="search">
            <views class="search-inner">
                <views class="icon">
                    <images :src="searchIcon"></images>
                </views>
                <views class="name" @click="handleVerify">请输入产品防伪码</views>
                <views class="code" @click="handleCode">
                    <images :src="codeIcon"></images>
                </views>
            </views>
        </views>
        <views class="menu">
            <views
                v-for="item in menuList"
                :key="item.name"
                class="menu-list">
                <views class="menu-icon">
                    <images :src="item.icon"></images>
                </views>
                <views class="menu-name">{{ item.name }}</views>
            </views>
        </views>
        <views class="prize-box">
            <views class="prize-banner" @click="handlePrize">
                <images :src="prizeBanner"></images>
            </views>
            <gift-list :list="giftList"></gift-list>
        </views>
        <views class="line"></views>
        <views class="article">
            <views class="article-title">
                <views class="name">农业知识</views>
                <views class="more" @click="handleMore">
                    更多
                    <images :src="moreIcon"></images>
                </views>
            </views>
            <views class="article-content">
                <article-item
                    v-for="item in articleList"
                    :key="item.id"
                    :item="item"
                    only-title
                    @click="handleDetail">
                </article-item>
            </views>
        </views>
        <views class="line"></views>
        <views class="other">
            <views class="other-title">
                <views class="name">其他</views>
            </views>
            <views class="other-content">
                <views
                    v-for="(item, index) in otherList"
                    :key="item.title"
                    class="other-item">
                    <views
                        :class="`bg${index + 1}`"
                        class="other-item-con">
                        <views class="name">{{ item.title }}</views>
                        <views class="des">{{ item.subTitle }}</views>
                        <views class="icon">
                            <images :src="item.icon"></images>
                        </views>
                    </views>
                </views>
            </views>
        </views>
        <views class="line"></views>
        <full-screen-ad
            v-if="showAd"
            v-model="showAd"
            :data="flashData"></full-screen-ad>
    </views>
</template>

<script>
import Banner from '@/components/banner'
import ArticleItem from '@/components/articleItem'
import FullScreenAd from '@/components/fullScreenAd'
import GiftList from './GiftList'
import tools from '@/mixins/tools'
import api from '@/utils/api'
export default {
    name: 'Home',
    components: {
        Banner,
        ArticleItem,
        FullScreenAd,
        GiftList
    },
    mixins: [tools],
    data() {
        return {
            showAd: false,
            prizeBanner: require('@/assets/images/sy_ggtxb@3x.png'),
            flashImg: require('@/assets/images/flash.jpg'),
            searchIcon: require('@/assets/images/sy_ss@3x.png'),
            codeIcon: require('@/assets/images/sy_sman@3x.png'),
            moreIcon: require('@/assets/images/awr.png'),
            bannerList: [],
            prizeBannerList: [],
            articleList: [],
            flashData: {},
            giftList: [],
            menuList: [
                {
                    name: '在线问答',
                    icon: require('@/assets/images/sy_zxwd@3x.png'),
                    url: ''
                },
                {
                    name: '农具知识',
                    icon: require('@/assets/images/sy_njzs@3x.png'),
                    url: ''
                },
                {
                    name: '产品中心',
                    icon: require('@/assets/images/sy_cpzx@3x.png'),
                    url: ''
                },
                {
                    name: '农化服务',
                    icon: require('@/assets/images/sy_nhfw@3x.png'),
                    url: ''
                }
            ],
            otherList: [
                {
                    title: '产品中心',
                    subTitle: '优质放心产品',
                    icon: require('@/assets/images/sy_cpzxa@3x.png'),
                    url: ''
                },
                {
                    title: '品牌文化',
                    subTitle: '阳光雨露撒可富',
                    icon: require('@/assets/images/sy_ppwh1@3x.png'),
                    url: ''
                },
                {
                    title: '官微互动',
                    subTitle: '欢迎留言互动',
                    icon: require('@/assets/images/sy_gwhd1@3x.png'),
                    url: ''
                },
                {
                    title: '农化服务',
                    subTitle: '诚信和谐创新',
                    icon: require('@/assets/images/sy_nhfw1@3x.png'),
                    url: ''
                }
            ]
        }
    },
    created() {
        this.init()
    },
    methods: {
        init() {
            this.getFlashList()
            this.getBanner()
            this.getPrizeBanner()
            this.getActivity()
            this.getArticle()
        },
        handleCode() {
            this.msg('开发中...')
        },
        getBanner() {
            api.getBanner({ position_type: 1 }).then((res) => {
                // this.bannerList = res.list
                this.bannerList = [
                    {
                        url: require('@/assets/images/sy_banner@3x.png')
                    },
                    {
                        url: require('@/assets/images/sy_banner@3x.png')
                    }
                ]
            })
        },
        getPrizeBanner() {
            api.getBanner({ position_type: 2 }).then((res) => {
                // this.prizeBannerList = res.list
            })
        },
        getActivity() {
            api.getActivity().then((res) => {
                this.getPrizeList(res.id)
            })
        },
        getPrizeList(id) {
            api.getPrizeList({ id }).then((res) => {
                // this.giftList = res.list
                this.giftList = [...res.list, ...res.list]
            })
        },
        getArticle() {
            const params = {
                page: 1,
                page_size: 3
            }
            api.getArticleList(params).then((res) => {
                this.articleList = res.list
            })
        },
        getFlashList() {
            api.getFlashList().then((res) => {
                if (res.list && res.list[0]) {
                    this.flashData = res.list[0]
                    this.flashData.url = this.flashImg
                    // this.flashData.seconds = 20
                    this.showAd = true
                }
            })
        },
        handleMore() {
            this.push('/article')
        },
        handleDetail(row) {
            this.push(`/article-detail?id=${row.id}&name=农业知识`)
        },
        handleVerify() {
            this.push('/verify?type=1&verify=1')
        },
        handlePrize() {
            this.push('/prize')
            // this.push('/verify?type=1&prize=1')
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
